body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:linear-gradient(109.6deg, #3ea1db 11.2%, #5d34ec 100.2%)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.menu-top{display:block  !important}.menu-top .ant-btn-primary{background-color:#1E2647 !important;color:#F8F3EF}.menu-top .gutter-row{color:azure !important}.menu-top .gutter-row h2{color:azure !important}.circular-menu{position:-webkit-sticky;position:sticky;width:100%;z-index:1 !important}.circular-menu .circular-menu.circular-menu-left{right:auto;left:1em}.circular-menu .circular-menu.circular-menu-left .ant-btn-circle.ant-btn-lg{min-width:60px !important;border-radius:50% !important;margin-left:10px !important;padding-left:10px !important}.circular-menu .logo{padding-top:5px}.circular-menu .logo .ant-image-img{width:100% !important;border-radius:10px !important}@media (min-width: 768px){.circular-menu .logo .ant-image-img{height:100px !important;width:90% !important}}.ant-drawer-content-wrapper{height:69px !important;padding:0px !important;margin:0px !important;align-items:center !important;justify-content:center !important}.ant-drawer-content-wrapper .ant-drawer-content{background-color:#1E2647 !important;padding:0px !important}

.contenido{align-items:center;padding:0% !important}

.ant-layout{background-color:transparent !important;padding-top:5px !important;height:87vh !important}.ant-layout .container{text-align:center !important}.ant-layout .container .ant-list{background-color:transparent !important}.ant-layout h1,.ant-layout h2{font-family:'Spectral SC', serif;font-size:4rem;font-weight:bold;color:#F8F3EF}.zoomInDown{animation-name:zoomInDown;animation-duration:1s;animation-fill-mode:both}@keyframes zoomInDown{0%{opacity:0;transform:scale3d(0.1, 0.1, 0.1) translate3d(0, -1000px, 0);animation-timing-function:cubic-bezier(0.55, 0.055, 0.675, 0.19)}60%{opacity:1;transform:scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);animation-timing-function:cubic-bezier(0.175, 0.885, 0.32, 1)}}

.chat{overflow-y:scroll !important;-webkit-overflow-scrolling:touch;background-color:whitesmoke !important;height:100px;width:100%;min-height:50vh;max-height:100vh;border:2px solid black;text-align:start}.chat p{font-size:1.1em;padding:0px;color:#2143b4 !important}.ant-tabs .ant-tabs-tabpane{height:70%}.ant-form .ant-input{width:100%}.ant-form .ant-input input{width:100%}

.contenedor{align-items:flex-start;padding:20%}.ant-form label{color:whitesmoke}.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn{color:azure !important}

